import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Fab } from '@material-ui/core';
import InfoIcon from '@material-ui/icons/Info';

const useStyles = makeStyles(theme => ({
    button: {
        position: "fixed",
        bottom: '30px',
        right: '10px',
        zIndex: 100,
        backgroundColor: 'rgb(37, 167, 167)'
    }
}));

export default function AlertDialog() {
    const classes = useStyles();

    const [open, setOpen] = React.useState(true);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <Fab className={classes.button} color="primary" aria-label="add" onClick={handleClickOpen}>
                <InfoIcon fontSize='large' />
            </Fab>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{"Velkommen"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Løsning til at vise ruteoptimering f.eks. ved tømning af glascontainere.
                        <ul>
                            <li>Tryk på markør-ikonet oppe i venstre hjørne og tryk derefter et sted på kortet for at lave en rute.</li>
                            <li>Gentag ovenstående for at udvide ruten</li>
                        </ul>
                        Ruten er sat til at stoppe og starte på Njalsgade og bruger <a href="http://project-osrm.org/" target="_blank">OSRM</a> til at løse Travelling Salesman problemet med data fra Open Street Map.
          </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary" autoFocus>
                        Ok
          </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}