import React, { useEffect, useRef, useState } from "react";
import mapboxgl from "mapbox-gl";
import MapboxDraw from '@mapbox/mapbox-gl-draw'
import "mapbox-gl/dist/mapbox-gl.css";
import "@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css"
import axios from 'axios'

const styles = {
    width: "100vw",
    height: "calc(100vh - 64px)",
    position: "absolute"
};

const Map = () => {
    const [map, setMap] = useState(null);
    // const [waypoints, setWaypoints] = useState([]);
    // const [route, setRoute] = useState({});
    const mapContainer = useRef(null);

    useEffect(() => {
        mapboxgl.accessToken = "pk.eyJ1IjoiYmFmZmlvc28iLCJhIjoiY2s0eHNsbXRoMDQwODNtbzZydGtheWRzayJ9.HRBBcfhegW6-bvmCAfxtvg";
        const initializeMap = ({ setMap, mapContainer }) => {
            const map = new mapboxgl.Map({
                container: mapContainer.current,
                style: "mapbox://styles/mapbox/light-v10", // stylesheet location
                center: [12.52489, 55.68168],
                zoom: 10
            });

            map.on("load", () => {
                setMap(map);
                map.resize();

                addDrawControl(map)
            });
        };

        if (!map) initializeMap({ setMap, mapContainer });
    }, [map]);

    const addDrawControl = (map) => {
        let draw = new MapboxDraw({
            displayControlsDefault: false,
            controls: {
                point: true,
                trash: true
            },
            // styles: [
            //     {
            //         'id': 'highlight-active-points',
            //         'type': 'circle',
            //         'filter': ['all',
            //             ['==', '$type', 'Point'],
            //             ['==', 'meta', 'feature'],
            //             ['==', 'active', 'true']],
            //         'paint': {
            //             'circle-radius': 10,
            //             'circle-color': '#000000'
            //         }
            //     },
            //     {
            //         'id': 'points-are-blue',
            //         'type': 'circle',
            //         'filter': ['all',
            //             ['==', '$type', 'Point'],
            //             ['==', 'meta', 'feature'],
            //             ['==', 'active', 'false']],
            //         'paint': {
            //             'circle-radius': 7,
            //             'circle-color': '#000088'
            //         }
            //     }
            // ]
        });
        map.addControl(draw, 'top-left');

        // Get drawn features and add to state
        const updatePoints = () => {
            let geom = draw.getAll();
            let points = geom.features.map(feat => feat.geometry.coordinates)

            if (points.length > 0) {
                let url = `http://165.22.200.0:5000/trip/v1/driving/12.578769,55.666729;${points.join(';')}?roundtrip=true&geometries=geojson&overview=full`
                axios.get(url).then(res => {
                    let linestring = res.data.trips[0].geometry
                    let waypoints = res.data.waypoints.map((item) => {
                        let feature = {
                            'type': 'Feature',
                            'geometry': {
                                'type': 'Point',
                                'coordinates': item.location
                            },
                            'properties': {
                                'id': item.waypoint_index + 1
                            }
                        }

                        return feature
                    })

                    // Remove current overlay
                    if (map.getSource('route') && map.getSource('waypoints')) {
                        map.removeLayer('route')
                        map.removeSource('route')
                        map.removeLayer('waypoints')
                        map.removeSource('waypoints')
                    }

                    map.addSource("route", {
                        "type": "geojson",
                        "data": {
                            'type': 'Feature',
                            'properties': {},
                            'geometry': linestring
                        }
                    });

                    map.addSource("waypoints", {
                        "type": "geojson",
                        "data": { type: "FeatureCollection", features: waypoints }
                    });

                    map.addLayer({
                        'id': 'route',
                        'type': 'line',
                        'source': 'route',
                        'layout': {
                            'line-join': 'round',
                            'line-cap': 'round'
                        },
                        'paint': {
                            'line-color': 'rgb(22,200,200)',
                            'line-width': 4
                        }
                    })

                    map.addLayer({
                        'id': 'waypoints',
                        'type': 'symbol',
                        'source': 'waypoints',
                        'layout': {
                            // get the icon name from the source's "icon" property
                            // concatenate the name to get an icon from the style's sprite sheet
                            'icon-image': 'beer-15',
                            // get the title name from the source's "title" property
                            'text-field': ['get', 'id'],
                            'text-font': ['Open Sans Semibold', 'Arial Unicode MS Bold'],
                            'text-offset': [0, 0.6],
                            'text-anchor': 'top'
                        }
                    })
                })
            }
        }

        // Store drawn poygon in state
        map.on('draw.create', updatePoints);
        map.on('draw.update', updatePoints);
        map.on('draw.delete', updatePoints);
    }

    // const getTrip = waypoints => {
    //     let url = `http://165.22.200.0:5000/route/v1/trip/${waypoints}?steps=false&geometries=geojson&overview=full&annotations=false`
    //     return axios.get(url).then(req => setRoute(req.data.routes[0].geometry))
    // }


    return <div ref={el => (mapContainer.current = el)} style={styles} />;
};

export default Map;